.unsere-standorte tr {
		display: block; }
.unsere-standorte thead tr  {
		position: absolute;
		top: -9999px;
		left: -9999px;}
.unsere-standorte tr  {border: 1px solid #ccc;
	margin-bottom:5px; }
.unsere-standorte td  { /* Behave  like a "row" */
		border: none;
		border-bottom: 1px solid #eee;
		position: relative;
		padding-left: 50%; }
.unsere-standorte td:before {
		/* Now like a table header */
		 /* position: absolute; */
		/* Top/left values mimic padding */
		top: 6px;
		float: left;
		left: 6px;
		width: 45%;
		padding-right: 10px;
		white-space: nowrap;}
/*
Label the data
*/
.unsere-standorte td:nth-of-type(1):before { content: "Niederlassung"; }
.unsere-standorte td:nth-of-type(2):before { content: "Homepage"; }
.unsere-standorte td:nth-of-type(3):before { content: "Email"; }
.unsere-standorte td:nth-of-type(4):before { content: "Telefon"; }
